@import url('https://fonts.googleapis.com/css2?family=League+Spartan&display=swap');

* {
  margin: 0;
  box-sizing: content-box;
  font-family: 'League Spartan', sans-serif;
}

.header {
  height: 40px;
  padding: 10px;
  background-color: #0d1117;
}

.title {
  margin-top: 10px;
  color: #4A98F7;
  text-align: center;
  position: relative;
  z-index: 2;
}

.title::before {
  content: " ";
  width: 272px;
  height: 55px;
  top: -15px;
  transform: translate(-20px,0);
  background-color: #161b22;
  border: 2px solid #89929b;
  border-radius: 7px;
  display: inline-block;
  position: absolute;
  z-index: -1;
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #0d1117;
  min-height: calc(100vh - 60px);
}

.board {
  border: 3px solid #89929b;
  border-radius: 7px;
}

.row {
  list-style: none;
}

.cell {
  width: 1em;
  height: 1em;
  color: #c6cdd5;
  font-size: 2em;
  border: 2px solid #89929b;
  background-color: #161b22;
  padding: 50px;
  float: left;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cell:hover {
  background-color: #4A98F7;
}

.turn-message {
  color: #c6cdd5;
  margin-bottom: 10px;
}

.reset-btn {
  font-size: 1em;
  background-color: #161b22;
  color: #c6cdd5;
  outline: none;
  border: 1px solid #89929b;
  border-radius: 7px;
  padding: 10px 20px;
  margin: 10px;
  transition: 0.6s ease;
}

.reset-btn:hover {
  color: #4A98F7;
  border-color: #4A98F7;
}

.fall {
  transform: translateY(136px);
}

.winner-message {
  position: absolute;
  font-size: 4em;
  font-weight: bold;
  color: #238636;
}

.winner-message::before {
  content: ""; /* no contenido */
  position: absolute; /* para posicionarlo en relaci√≥n al h1 */
  z-index: -1; /* para que aparezca debajo del h1 */
  top: 0; /* arriba del h1*/
  left: 0; /* a la izquierda del h1 */
  width: 100%; /* ancho */
  height: 100%; /* alto */
  box-shadow: 0 0 5px #ff0; /* sombra */
}